@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
body {
  display: gird;
  background: url(/static/media/lawless-capture-XKvXTRi_13g-unsplash.11131be1.jpeg);
  background-size: cover;
}

.folderContainer {
  font-family: "Montserrat";
  color: lightcyan;
  position: fixed;
  top: 30%;
  left: 10%;
}
.canvasContainer {
  font-family: "Montserrat";
  color: lightcyan;
  position: fixed;
  top: 60%;
  left: 10%;
}

nav {
  font-family: "Montserrat";
  align-items: center;

  margin-left: 40px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 1400px;
}
p {
  color: lightcyan;
  margin-left: 30px;
  margin-right: 30px;
}

.cards {
  padding-top: 25px;
  padding-bottom: 50px;
  justify-content: center;
  transition: 0.3s;
  width: 250px;
  height: 200px;
}

.subButton {
  margin-top: 5px;
  margin-bottom: 25px;
  border-radius: 8px;
}

.subButton:hover {
  background-color: rgb(0, 255, 42);
  color: white;
  transition: 0.5s;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 85%;
  margin-top: 10%;
  margin-left: 10%;
  margin-right: 10%;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  background-color: rgb(31, 29, 29);
}

.font {
  font-family: "Montserrat";
}

h2 {
  color: lightcyan;
  text-transform: uppercase;
}

.aboutcontainer {
  font-family: "Montserrat";
  color: lightcyan;
  /* background-color: lightgray; */
  position: fixed;
  top: 15%;
  left: 5%;
  width: 600px;
  border-style: solid;
  border-bottom-right-radius: 8px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 15px;
  /* transform: translate(-50%, -50%); */
  /* border-bottom-left-radius: 8px;
   */
}
.aboutvideo {
  position: fixed;
  top: 15%;
  left: 50%;
}

h1 {
  font-size: 3em;
  margin-left: 200px;
  margin-bottom: 0;
  color: lightcyan;
}
h3 {
  color: lightcyan;
}
.center-text {
  margin-left: 180px;
}
.centered {
  justify-content: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.column {
  float: left;
  height: 60%;
  width: 60%;
  padding: 0 10px;
  margin-left: 120px;
}
.row {
  margin: 0 -5px;
}
.card {
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); this adds the "card" effect */
  padding: 16px;
  justify-content: center;
  margin-left: 40px;
  border-style: solid;
  border-color: lightcyan;
  border-bottom-right-radius: 8px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  text-align: center;
  /* background-color: rgba(245, 245, 245, 1); */
  /* opacity: 0.4; */
  margin-bottom: 10px;
}

